import { XMLParser } from 'fast-xml-parser';

/**
 * Routing XML dosyasndan ham veriyi okur ve JSON objesine dntrr.
 * Dosyann Routing XML yapsna uygun olup olmadn kontrol eder.
 * @param {File} file - Yklenen Routing XML dosyas.
 * @returns {Promise<Object>} - Ham XML verisinin JSON hali.
 * @throws {Error} - Dosya okuma, ayrtrma veya Routing yaps kontrolnde hata oluursa.
 */
export const parseRoutingXML = (file) => {
    return new Promise((resolve, reject) => {
        if (!file) {
            reject(new Error("Yklenecek bir dosya seilmedi."));
            return;
        }

        const fileExtension = file.name.split('.').pop().toLowerCase();
        if (fileExtension !== 'xml') {
            reject(new Error(`Sadece XML dosyalar yklenebilir. Yklenen: ${file.name}`));
            return;
        }

        const reader = new FileReader();
        reader.onload = (e) => {
            try {
                const xmlString = e.target.result;
                const options = {
                    ignoreAttributes: false,
                    attributeNamePrefix: "@",
                    textNodeName: "#text"
                };

                const parser = new XMLParser(options);
                const json = parser.parse(xmlString);

                if (!json?.ROUTING_DETAILS?.LIST_G_GROUP1_SORT?.G_GROUP1_SORT?.LIST_G_GROUP2_SORT?.G_GROUP2_SORT?.LIST_G_ROOM_NO?.G_ROOM_NO) {
                    throw new Error(`Geerli bir routing_details dosyas ykleyin.`);
                }

                resolve(json);
            } catch (error) {
                // XML ayrtrma hatas veya yapsal hata
                reject(new Error(error.message));
            }
        };
        reader.onerror = (error) => {
            reject(new Error(`"${file.name}" dosyas okuma hatas: ${error.target.error?.message || error.target.error || 'Bilinmeyen hata'}`));
        };
        reader.readAsText(file);
    });
};

/**
 * Ham Routing XML verisinden ilenmi veri dizisini oluturur.
 * Bu fonksiyon, uygulamann kullanabilecei belirli alanlar ayklayarak bir dizi dndrr.
 * @param {Object} rawData - Ham Routing XML verisinin JSON hali.
 * @returns {Array<Object>} - stediiniz alanlar ieren ilenmi veri objeleri dizisi.
 */
export const transformRoutingData = (rawData) => {
    let transformedRecords = [];

    try {
        const records = rawData?.ROUTING_DETAILS?.LIST_G_GROUP1_SORT?.G_GROUP1_SORT?.LIST_G_GROUP2_SORT?.G_GROUP2_SORT?.LIST_G_ROOM_NO?.G_ROOM_NO || [];
        const recordsArray = Array.isArray(records) ? records : [records]; // Tek kayt varsa diziye evir

        // Helper function: Bo string yerine sadece null/undefined ise bo string atar, 0' korur.
        const getValueOrEmptyString = (value) => {
            return value === null || value === undefined ? '' : value;
        };
        transformedRecords = recordsArray
            .filter(record => {
                const roomNoValue = getValueOrEmptyString(record.ROOM_NO); // Bu etiket adn kontrol et!
                const roomNoInt = parseInt(roomNoValue, 10);
                const shortResvStatus = getValueOrEmptyString(record.SHORT_RESV_STATUS).toUpperCase(); 

                // Oda numarasnn bo olmadn ve 9000'den byk olmadn kontrol et
                const isRoomNoValid = roomNoValue !== '' && (isNaN(roomNoInt) || roomNoInt < 9000);

                const isCheckedIn = shortResvStatus === 'CKIN';

                return isRoomNoValid && isCheckedIn; // Her iki koulun da salanmas gerekiyor
            })
            .map((record, index) => {
                return {
                    roomNo: getValueOrEmptyString(record.ROOM_NO),
                    routingList: getValueOrEmptyString(record.LIST_G_ROUTING), // Bu alann ierii muhtemelen daha karmak bir obje veya dizi olacak, dikkat!
                    fullName: getValueOrEmptyString(record.FULL_NAME),
                };
            })
            .sort((a, b) => {
                const roomNoA = parseInt(a.roomNo, 10);
                const roomNoB = parseInt(b.roomNo, 10);

                // Geersiz (NaN) oda numaralarn sona at veya zel bir durumla ele al
                if (isNaN(roomNoA) && isNaN(roomNoB)) return 0; // kisi de geersizse sra nemli deil
                if (isNaN(roomNoA)) return 1; // A geersizse B'den sonra gelsin
                if (isNaN(roomNoB)) return -1; // B geersizse A'dan sonra gelsin

                return roomNoA - roomNoB; // Saysal olarak kkten bye srala
            });

    } catch (error) {
        console.error("transformRoutingData'da kritik hata olutu:", error);
        throw new Error("Routing veri dnm srasnda kritik bir hata olutu: " + error.message);
    }

    return transformedRecords;
};