// src/components/ErrorDetailsArea.jsx
import React from 'react';
import { Card, Button, Alert, Spinner } from 'react-bootstrap';
import { useData } from '../context/DataContext';

function ErrorDetailsArea() {
  // Hatalar, gncelleme fonksiyonunu ve ilgili tm raw data state'lerini DataContext'ten alyoruz
  const { 
    generalOperaErrorsData, 
    setGeneralOperaErrorsData, 
    rawInhouseData, // Her bir raw data'y ektik
    rawKBSData,
    rawPolisRaporuData,
    rawRoutingData,
    rawCashringData,
  } = useData();

  // Herhangi bir dosyann yklenip yklenmediini kontrol eden yeni bir flag
  // raw data state'lerinden herhangi biri null deilse, bir dosya yklenmi demektir.
  const hasAnyFileUploaded = 
    rawInhouseData !== null ||
    rawKBSData !== null ||
    rawPolisRaporuData !== null ||
    rawRoutingData !== null ||
    rawCashringData !== null;

  // Hata silme ilevi: Seilen hatay listeden kaldrr
  const handleDeleteError = (idToRemove) => {
    const updatedErrors = generalOperaErrorsData.filter((error) => error.id !== idToRemove);
    setGeneralOperaErrorsData(updatedErrors);
  };

  return (
    <Card className="shadow-sm">
      <Card.Body>
        <Card.Title className="text-danger mb-3">
          <i className="bi bi-exclamation-triangle-fill me-2"></i> Genel Hatalar
        </Card.Title>
        <div 
          style={{ 
            height: '600px', 
            overflowY: 'auto',  
            paddingRight: '10px'
          }}
          className="error-list-container"
        >
          {/* hasAnyFileUploaded durumuna gre ierii koullu olarak render etme */}
          {!hasAnyFileUploaded ? (
            // 1. Durum: Henz hibir dosya yklenmediyse
            <div className="text-info lead text-center">
              <Spinner animation="border" size="sm" className="me-2" />
              **Bir dosya yklenmesi bekleniyor...**
            </div>
          ) : (
            // 2. Durum: En az bir dosya yklendiyse (kontrollerin balam veya bitmi olmas beklenir)
            generalOperaErrorsData.length > 0 ? ( 
              // Hatalar varsa, hata listesini gster
              <ul className="list-unstyled">
                {generalOperaErrorsData.map((error) => ( 
                  <li 
                    key={error.id || `${error.type}-${error.message}`} 
                    className="d-flex justify-content-between align-items-center mb-2 p-2 border rounded"
                    style={{ backgroundColor: '#fff3cd', borderColor: '#ffc107', color: '#856404' }}
                  >
                    <span className="me-auto text-break">{error.message}</span>
                    <Button
                      variant="link"
                      className="text-danger p-0 ms-2"
                      onClick={() => handleDeleteError(error.id)} 
                      aria-label="Hatay Sil"
                    >
                      <i className="bi bi-x-circle-fill fs-5"></i>
                    </Button>
                  </li>
                ))}
              </ul>
            ) : (
              // Hata yoksa, baar mesajn gster
              <Alert variant="success" className="text-center">
                <i className="bi bi-check-circle-fill me-2"></i> Harika! Hibir genel hata bulunamad.
              </Alert>
            )
          )}
        </div>
      </Card.Body>
    </Card>
  );
}

export default ErrorDetailsArea;