// src/utils/processCashringData.js
import { XMLParser } from 'fast-xml-parser';

/**
 * Cashring (p2009) XML dosyasndan ham veriyi okur ve JSON objesine dntrr.
 * Dosyann Cashring XML yapsna uygun olup olmadn kontrol eder.
 * @param {File} file - Yklenen Cashring XML dosyas.
 * @returns {Promise<Object>} - Ham XML verisinin JSON hali.
 * @throws {Error} - Dosya okuma, ayrtrma veya Cashring yaps kontrolnde hata oluursa.
 */
export const parseCashringXML = (file) => {
    return new Promise((resolve, reject) => {
        if (!file) {
            reject(new Error("Yklenecek bir dosya seilmedi."));
            return;
        }

        const fileExtension = file.name.split('.').pop().toLowerCase();
        if (fileExtension !== 'xml') {
            reject(new Error(`Sadece XML dosyalar yklenebilir. Yklenen: ${file.name}`));
            return;
        }

        const reader = new FileReader();
        reader.onload = (e) => {
            try {
                const xmlString = e.target.result;
                const options = {
                    ignoreAttributes: false,
                    attributeNamePrefix: "@",
                    textNodeName: "#text"
                };

                const parser = new XMLParser(options);
                const json = parser.parse(xmlString);


                if (!json?.P2009_BALANCEBYWINDOW?.LIST_G_ROOM?.G_ROOM) { 
                    throw new Error(`Geerli bir Cashring dosyas ykleyin.`);
                }

                resolve(json);
            } catch (error) {
                // XML ayrtrma hatas veya yapsal hata
                reject(new Error(`Cashring XML dosyas ayrtrma veya yap kontrol hatas: ${error.message}`));
            }
        };
        reader.onerror = (error) => {
            reject(new Error(`"${file.name}" dosyas okuma hatas: ${error.target.error?.message || error.target.error || 'Bilinmeyen hata'}`));
        };
        reader.readAsText(file);
    });
};

/**
 * Ham Cashring XML verisinden ilenmi veri dizisini oluturur.
 * Bu fonksiyon, uygulamann kullanabilecei belirli alanlar ayklayarak bir dizi dndrr.
 * @param {Object} rawData - Ham Cashring XML verisinin JSON hali.
 * @returns {Array<Object>} - stediiniz alanlar ieren ilenmi veri objeleri dizisi.
 */
export const transformCashringData = (rawData) => {
    let transformedRecords = [];

    try {
        // Varsaym: P2009_CASHIER_CLOSURE?.LIST_G_TRANSACTIONS?.G_TRANSACTIONS
        const transactions = rawData?.P2009_BALANCEBYWINDOW?.LIST_G_ROOM?.G_ROOM || [];
        const transactionsArray = Array.isArray(transactions) ? transactions : [transactions]; // Tek kayt varsa diziye evir

        // Helper function: Bo string yerine sadece null/undefined ise bo string atar, 0' korur.
        const getValueOrEmptyString = (value) => {
            return value === null || value === undefined ? '' : value;
        };

        transformedRecords = transactionsArray.map((transaction, index) => {
            // kendi gerek Cashring XML yapndaki etiket isimlerine gre ayarlamalsn!
            return {
                accRate: getValueOrEmptyString(transaction.ACC_RATE), 
                balance: getValueOrEmptyString(transaction.BALANCE), 
                roomNo: getValueOrEmptyString(transaction.ROOM), 
                PM: getValueOrEmptyString(transaction.PM), 
                variance: getValueOrEmptyString(transaction.VARIANCE),
                win1: getValueOrEmptyString(transaction.WINDOW1),
                win2: getValueOrEmptyString(transaction.WINDOW2),
                win3: getValueOrEmptyString(transaction.WINDOW3),
                win4: getValueOrEmptyString(transaction.WINDOW4),
                // ... Cashring dosyasndan almanz gereken dier alanlar buraya eklenecek
            };
        });

    } catch (error) {
        console.error("transformCashringData'da kritik hata olutu:", error);
        throw new Error("Cashring veri dnm srasnda kritik bir hata olutu: " + error.message);
    }

    return transformedRecords;
};