// src/utils/kbsOperaCompare.js
// Bu dosya, KBS ve Polis Raporu (OPERA) verilerini karlatrmak iin kullanlr.

/**
 * Oda numaralarn ve belge numaralarn karlatrma iin normalletirir.
 * Boluklar kaldrr ve kk harfe evirir.
 * @param {string|number} value - Normalletirilecek deer.
 * @returns {string} - Normalletirilmi deer.
 */
const normalizeValue = (value) => {
    if (value === null || value === undefined) return '';
    return String(value).replace(/\s/g, '').toLowerCase();
};

/**
 * KBS ve Polis Raporu verilerini karlatrr ve uyumsuzluklar tespit eder.
 *
 * @param {Array<Object>} kbsData - lenmi KBS verisi.
 * @param {Array<Object>} policeReportData - lenmi Polis Raporu (OPERA) verisi.
 * @returns {Object} - Oda numaralarna gre gruplandrlm hata objeleri.
 * rn: { "502": [{ type: "KBS_MISMATCH", kbsGuest: {...}, operaGuest: {...} }, ...], ... }
 */
export const compareKBSAndPoliceReportData = (kbsData, policeReportData) => {
    const kbsErrors = {};

    // Her iki veri kmesini de belge numaralarna gre hzl eriim iin haritala
    const kbsMap = new Map(); // key: normalizedBelgeNo, value: Array<guest>
    const operaMap = new Map(); // key: normalizedBelgeNo, value: Array<guest>

    // KBS verisini haritala
    kbsData.forEach(guest => {
        const normalizedBelgeNo = normalizeValue(guest.belgeNo);
        if (!kbsMap.has(normalizedBelgeNo)) {
            kbsMap.set(normalizedBelgeNo, []);
        }
        kbsMap.get(normalizedBelgeNo).push(guest);
    });

    // OPERA verisini haritala
    policeReportData.forEach(guest => {
        const normalizedBelgeNo = normalizeValue(guest.belgeNo);
        if (!operaMap.has(normalizedBelgeNo)) {
            operaMap.set(normalizedBelgeNo, []);
        }
        operaMap.get(normalizedBelgeNo).push(guest);
    });

    // --- KBS'den OPERA'ya Dorulama (KBS'de olup OPERA'da olmayan veya uyumsuz olanlar) ---
    kbsData.forEach(kbsGuest => {
        const normalizedKBSBelgeNo = normalizeValue(kbsGuest.belgeNo);
        const normalizedKBSOdaNo = normalizeValue(kbsGuest.odaNo);

        // Belge numarasna gre OPERA'da eleenleri bul
        const matchingOperaGuests = operaMap.get(normalizedKBSBelgeNo) || [];

        let foundMatchInOpera = false;
        for (const operaGuest of matchingOperaGuests) {
            const normalizedOperaOdaNo = normalizeValue(operaGuest.roomNo);

            // Hem belge no hem de oda no eleiyor mu?
            if (normalizedKBSOdaNo === normalizedOperaOdaNo) {
                // Ad ve soyad karlatrmas (normalletirilmi halleriyle)
                const normalizedKBSAdSoyad = `${normalizeValue(kbsGuest.ad)} ${normalizeValue(kbsGuest.soyad)}`;
                const normalizedOperaAdSoyad = `${normalizeValue(operaGuest.firstName)} ${normalizeValue(operaGuest.lastName)}`;

                if (normalizedKBSAdSoyad === normalizedOperaAdSoyad) {
                    foundMatchInOpera = true;
                    // Tamamen uyumlu, hata yok
                    break;
                } else {
                    // Belge No ve Oda No eleiyor ama Ad/Soyad uyumsuz
                    if (!kbsErrors[normalizedKBSOdaNo]) {
                        kbsErrors[normalizedKBSOdaNo] = [];
                    }
                    
                    // Ad/Soyad uyumsuzluunu iki ayr hata olarak ekle
                    // 1. KBS'deki misafiri, sanki OPERA'da yokmu gibi ekle
                    kbsErrors[normalizedKBSOdaNo].push({
                        type: 'KBS_MISSING_IN_OPERA', // Mevcut "KBS'de eksik" tipini kullanabiliriz
                        message: `Ad/Soyad Uyumsuz (KBS Taraf Eksik): KBS: '${kbsGuest.ad} ${kbsGuest.soyad}', Belge No: '${kbsGuest.belgeNo}'`,
                        kbsGuest: kbsGuest,
                        operaGuest: null, // OPERA taraf bo kalsn
                        source: 'KBS' 
                    });

                    // 2. OPERA'daki misafiri, sanki KBS'de yokmu gibi ekle
                    kbsErrors[normalizedKBSOdaNo].push({
                        type: 'OPERA_MISSING_IN_KBS', // Mevcut "OPERA'da eksik" tipini kullanabiliriz
                        message: `Ad/Soyad Uyumsuz (OPERA Taraf Eksik): OPERA: '${operaGuest.firstName} ${operaGuest.lastName}', Belge No: '${operaGuest.belgeNo}'`,
                        kbsGuest: null, // KBS taraf bo kalsn
                        operaGuest: { 
                            ad: operaGuest.firstName, 
                            soyad: operaGuest.lastName, 
                            belgeNo: operaGuest.belgeNo, 
                            odaNo: operaGuest.roomNo 
                        }, 
                        source: 'OPERA' 
                    });

                    foundMatchInOpera = true; // Uyumsuz olsa da bir eleme bulundu
                    break; // Sadece bir eleen bulmak yeterli
                }
            }
        }

        if (!foundMatchInOpera) {
            // KBS'de var ama OPERA'da ilgili belge numaras veya oda numarasnda eleme yok
            if (!kbsErrors[normalizedKBSOdaNo]) {
                kbsErrors[normalizedKBSOdaNo] = [];
            }
            kbsErrors[normalizedKBSOdaNo].push({
                type: 'KBS_MISSING_IN_OPERA',
                message: `KBS Kayd OPERA'da Eksik: Ad: '${kbsGuest.ad}', Soyad: '${kbsGuest.soyad}', Belge No: '${kbsGuest.belgeNo}'`,
                kbsGuest: kbsGuest,
                operaGuest: null, // OPERA tarafnda eleme yok
                source: 'KBS' // Hata KBS'den kaynaklanyor
            });
        }
    });

    // --- OPERA'dan KBS'ye Dorulama (OPERA'da olup KBS'de olmayanlar) ---
    // NOT: Ad/Soyad uyumsuzluklar (NAME_MISMATCH) yukardaki KBS dngsnde zaten iki ayr hata olarak ilendii iin
    // burada tekrar belgeNo ve odaNo eleenleri kontrol edip 'NAME_MISMATCH' hatas eklemeye gerek yoktur.
    // Burada sadece OPERA'da olup KBS'de olmayanlar kontrol edeceiz.
    policeReportData.forEach(operaGuest => {
        const normalizedOperaBelgeNo = normalizeValue(operaGuest.belgeNo);
        const normalizedOperaOdaNo = normalizeValue(operaGuest.roomNo);

        const matchingKBSGuests = kbsMap.get(normalizedOperaBelgeNo) || [];
        
        // Bu OPERA kaydnn KBS'de belgeNo ve odaNo olarak tam bir elemesi var m?
        // Ve bu eleen kaydn ad/soyad da uyumlu mu?
        const isFullyMatchedInKBS = matchingKBSGuests.some(kbsGuest => 
            normalizeValue(kbsGuest.odaNo) === normalizedOperaOdaNo &&
            normalizeValue(kbsGuest.ad) === normalizeValue(operaGuest.firstName) &&
            normalizeValue(kbsGuest.soyad) === normalizeValue(operaGuest.lastName)
        );

        // Eer tam eleme yoksa (yani KBS'de hi yoksa veya belge/oda no eleiyor ama ad/soyad farklysa)
        // ad/soyad farkll yukarda zaten ele alnd iin, burada sadece "tamamen eksik" durumuna bakyoruz.
        if (!isFullyMatchedInKBS) {
            // Ayrca, bu OPERA kaydnn bir NAME_MISMATCH olarak zaten eklenip eklenmediini kontrol etmeliyiz.
            // Yani, belge no ve oda no eleiyor ama ad/soyad farkl olduu iin yukarda zaten ikiye ayrlm m?
            const isAlreadyHandledAsNameMismatch = matchingKBSGuests.some(kbsGuest =>
                normalizeValue(kbsGuest.odaNo) === normalizedOperaOdaNo &&
                (normalizeValue(kbsGuest.ad) !== normalizeValue(operaGuest.firstName) || 
                 normalizeValue(kbsGuest.soyad) !== normalizeValue(operaGuest.lastName))
            );

            if (!isAlreadyHandledAsNameMismatch) {
                if (!kbsErrors[normalizedOperaOdaNo]) {
                    kbsErrors[normalizedOperaOdaNo] = [];
                }
                
                // Mkerrer hata eklemeyi nlemek iin kontrol et
                const isDuplicateMissing = kbsErrors[normalizedOperaOdaNo].some(error =>
                    error.type === 'OPERA_MISSING_IN_KBS' &&
                    normalizeValue(error.operaGuest.belgeNo) === normalizedOperaBelgeNo &&
                    // Sadece belge no ayn olanlar kontrol etmiyoruz, nk ad/soyad uyumsuzluunda da belge no ayn olabilir.
                    // Burada gerekten sadece "KBS'de tamamen yok" durumu iin kontrol yapmalyz.
                    // Ad/soyad uyumsuzluu iin eklenen hatalar farkl 'type' veya 'source' ile eklenmeli.
                    error.kbsGuest === null // KBS taraf bo olanlar (yani KBS'de eksik olanlar)
                );

                if (!isDuplicateMissing) {
                    kbsErrors[normalizedOperaOdaNo].push({
                        type: 'OPERA_MISSING_IN_KBS',
                        message: `OPERA Kayd KBS'de Eksik: Ad: '${operaGuest.firstName}', Soyad: '${operaGuest.lastName}', Belge No: '${operaGuest.belgeNo}'`,
                        kbsGuest: null, 
                        operaGuest: { 
                            ad: operaGuest.firstName, 
                            soyad: operaGuest.lastName, 
                            belgeNo: operaGuest.belgeNo, 
                            odaNo: operaGuest.roomNo 
                        }, 
                        source: 'OPERA' 
                    });
                }
            }
        }
    });
    
    // Hatalar oda numarasna gre srala ve her odadaki hatalar misafir adna gre srala
    const sortedKbsErrors = {};
    Object.keys(kbsErrors).sort((a, b) => {
        const numA = parseInt(a, 10);
        const numB = parseInt(b, 10);
        if (!isNaN(numA) && !isNaN(numB)) {
            return numA - numB;
        }
        return a.localeCompare(b);
    }).forEach(roomNo => {
        // Her odadaki hata dizisini adna gre srala
        sortedKbsErrors[roomNo] = kbsErrors[roomNo].sort((a, b) => {
            const nameA = a.kbsGuest ? normalizeValue(a.kbsGuest.ad) : normalizeValue(a.operaGuest ? a.operaGuest.ad : '');
            const nameB = b.kbsGuest ? normalizeValue(b.kbsGuest.ad) : normalizeValue(b.operaGuest ? b.operaGuest.ad : '');
            return nameA.localeCompare(nameB);
        });
    });

    return sortedKbsErrors;
};