// src/utils/processInhouseData.js
import { XMLParser } from 'fast-xml-parser';

/**
 * Yklenen dosyann inhouse.xml olup olmadn ve bir XML dosyas olup olmadn kontrol eder.
 * @param {File} file - Kontrol edilecek dosya.
 * @returns {void} - Dosya uygun deilse hata frlatr.
 * @throws {Error} - Dosya inhouse.xml veya XML deilse hata frlatr.
 */
/**
 * inhouse.xml dosyasndan ham veriyi okur ve JSON objesine dntrr.
 * @param {File} file - Yklenen inhouse.xml dosyas.
 * @returns {Promise<Object>} - Ham XML verisinin JSON hali.
 */

export const parseInhouseXML = (file) => {
  return new Promise((resolve, reject) => {
    if (!file) {
      reject(new Error("Yklenecek bir dosya seilmedi."));
      return;
    }

    // Dosya uzants kontrol hala yaplabilir, ancak asl kontrol XML ieriinde olacak.
    const fileExtension = file.name.split('.').pop().toLowerCase();
    if (fileExtension !== 'xml') {
      reject(new Error(`Sadece XML dosyalar yklenebilir. Yklenen: ${file.name}`));
      return;
    }

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const xmlString = e.target.result;
        const options = {
          ignoreAttributes: false,
          attributeNamePrefix: "@",
          textNodeName: "#text",
        };

        const parser = new XMLParser(options);
        const json = parser.parse(xmlString);
        
        if (!json?.GIBYROOM?.LIST_G_ROOM?.G_ROOM) {
          throw new Error(`Geerli bir inhouse dosyas ykleyin`);
        }

        resolve(json);
      } catch (error) {
        reject(new Error(error.message));
      }
    };
    reader.onerror = (error) => {
      reject(new Error('Dosya okuma hatas: ' + error.target.error));
    };
    reader.readAsText(file);
  });
};

/**
 * Ham inhouse.xml verisinden dorudan ilenmi veri dizisini oluturur.
 * Bu fonksiyon sadece belirtilen alanlar ieren bir dizi dndrr.
 * Hata ve genel bilgi toplama sorumluluu DataContext'e braklr.
 * @param {Object} rawData - Ham inhouse.xml verisinin JSON hali.
 * @returns {Array<Object>} - stediiniz alanlar ieren ilenmi veri objeleri dizisi.
 */
export const transformInhouseData = (rawData) => {
  let transformedRecords = []; // Bu dorudan ilenmi kaytlar tutacak

  try {
    const inhouseRooms = rawData?.GIBYROOM?.LIST_G_ROOM?.G_ROOM || [];
    const roomsArray = Array.isArray(inhouseRooms) ? inhouseRooms : [inhouseRooms];


     // Saysal deerleri kontrol eden yardmc fonksiyon
     const getValueOrEmptyString = (value) => {
      // Deer null veya undefined ise bo string dndr, aksi takdirde deeri olduu gibi kullan
      // Not: 0 deeri false olmad iin bu kontrol 0' koruyacaktr.
      return (value === null || value === undefined) ? '' : value;
    };


    transformedRecords = roomsArray.map((room, index) => {
      return {
        roomNo: getValueOrEmptyString(room.ROOM),
        name: getValueOrEmptyString(room.FULL_NAME),
        companyName: getValueOrEmptyString(room.COMPANY_NAME),
        rateCode: getValueOrEmptyString(room.RATE_CODE),
        rate: getValueOrEmptyString(room.SHARE_AMOUNT),
        accompanyNames: getValueOrEmptyString(room.ACCOMPANYING_NAMES),
        paymentMethod: getValueOrEmptyString(room.PAYMENT_METHOD),
        currencyCode: getValueOrEmptyString(room.CURRENCY_CODE),
        adults: getValueOrEmptyString(room.ADULTS),
        children: getValueOrEmptyString(room.CHILDREN),
        balance: getValueOrEmptyString(room.BALANCE),
        departureDate: getValueOrEmptyString(room.DEPARTURE),
        arrivalDate: getValueOrEmptyString(room.ARRIVAL),
        comment: getValueOrEmptyString(room.LIST_G_COMMENT_RESV_NAME_ID)
      };
    });

  } catch (error) {
    // Burada sadece dnmn kendisinde oluan kritik hatalar yakalarz
    // Ve bu hatay frlatrz, DataContext onu yakalar ve generalErrorsData'ya ekler.
    console.error("transformInhouseData'da kritik hata olutu:", error);
    throw new Error("Veri dnm srasnda kritik hata olutu: " + error.message);
  }

  // Sadece ilenmi kaytlar dizisini dndryoruz
  return transformedRecords;
};