// src/controls/basicControls.js

import { v4 as uuidv4 } from 'uuid';

/**
 * checkMissingBirthDates:
 * Polis Raporu verisindeki her misafiri kontrol ederek doum tarihi bo olanlar bulur.
 *
 * @param {Array} polisRaporuData - lenmi Polis Raporu verileri (misafir listesi).
 * @returns {Array} - Bulunan bo doum tarihi hatalarnn listesi.
 */
export const checkMissingBirthDates = (polisRaporuData) => {
    const errors = [];
    
    if (!polisRaporuData || polisRaporuData.length === 0) {
        return errors; // Veri yoksa bo dizi dndr
    }

    polisRaporuData.forEach(guest => {
        // 'dogumTarihi' alan bosa veya sadece boluklardan oluuyorsa
        if (!guest.birthDate || String(guest.birthDate).trim() === '') {
            errors.push({
                type: 'MISSING_BIRTH_DATE',
                message: `Oda No: ${guest.roomNo || '-'}, Misafir: ${guest.firstName || '-'} ${guest.lastName || '-' } iin doum tarihi bo.`
                // Dier ilgili misafir bilgileri buraya eklenebilir
            });
        }
    });

    return errors;
};


/**
 * checkGuestCountConsistency:
 * Inhouse verisindeki rezervasyonlarda beyan edilen kii says ile
 * isim listesindeki (name ve accompanyNames) kii saysnn tutarlln kontrol eder.
 *
 * @param {Array} processedInhouseData - lenmi Inhouse.txt verileri.
 * @returns {Array} - Bulunan kii says uyumazl hatalarnn listesi.
 */
export const checkGuestCountConsistency = (processedInhouseData) => {
    const errors = [];

    if (!processedInhouseData || processedInhouseData.length === 0) {
        return errors; // Veri yoksa bo dizi dndr
    }
    
    processedInhouseData.forEach(guest => {
        // Rezervasyonda belirtilen toplam kii says (adults + children)
        const declaredGuestCount = (guest.adults || 0) + (guest.children || 0);

        // accompanyNames alanndaki kii saysn bulma
        let accompaniedGuestCount = 0;
        if (guest.accompanyNames && typeof guest.accompanyNames === 'string' && guest.accompanyNames.trim() !== '') {
            const accompaniedNames = guest.accompanyNames.split('/').map(name => name.trim()).filter(name => name !== '');
            accompaniedGuestCount = accompaniedNames.length;
        }

        // Toplam isim says (name + accompanyNames)
        // name alan her zaman tek bir kii olduu iin 1 ekliyoruz.
        const totalNamedGuests = 1 + accompaniedGuestCount;

        // Beyan edilen kii says ile isim says uyumuyor mu?
        if (declaredGuestCount !== totalNamedGuests) {
            errors.push({
                id: `guestcount-error-${guest.roomNo}-${guest.name}-${Date.now()}-${Math.random()}`,
                type: 'GUEST_COUNT_MISMATCH',
                message: `Oda No: ${guest.roomNo || '-'} iin kii says uyumuyor.`
            });
        }
    });

    return errors;
};


/**
 * Polis Raporu verisindeki TC/Pasaport bilgilerini kontrol eder.
 * @param {Array<Object>} processedPolisRaporuData - lenmi Polis Raporu verisi.
 * @returns {Array<Object>} Bulunan hatalarn listesi.
 */
export const checkTcPassportConsistency = (processedPolisRaporuData) => {
    const errors = [];

    // Verilerin bo olup olmadn kontrol et
    if (!processedPolisRaporuData || processedPolisRaporuData.length === 0) {
        return errors;
    }

    processedPolisRaporuData.forEach(guest => {
        const guestName = `${guest.firstName} ${guest.lastName}`;
        
        // Kontrol 1, 2, 3, 4: Bo alan kontrol
        if (!guest.belgeNo) {
            errors.push({ id: uuidv4(), type: 'MISSING_BELGENO', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin belge no bo.`, guest });
        }
        if (!guest.belgeTuru) {
            errors.push({ id: uuidv4(), type: 'MISSING_BELGETURU', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin belge tr bo.`, guest });
        }
        if (!guest.ikametAdresi) {
            errors.push({ id: uuidv4(), type: 'MISSING_IKAMET_ADRESI', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin ikamet adresi bo.`, guest });
        }
        if (!guest.uyruk) {
            errors.push({ id: uuidv4(), type: 'MISSING_UYRUK', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin uyruk bo.`, guest });
        }

        // Eer temel alanlar eksikse, detayl kontrolleri atla
        if (!guest.belgeTuru || !guest.belgeNo || !guest.uyruk || !guest.ikametAdresi) {
            return;
        }

        // Kontrol 5, 6, 7: Belge tr "TCKN" olanlar iin
        if (guest.belgeTuru === 'TCKN') {
            const isTc = guest.belgeNo.length === 11 && !isNaN(guest.belgeNo);
            const startsWith9x = guest.belgeNo.startsWith('99') || guest.belgeNo.startsWith('98') || guest.belgeNo.startsWith('97');

            // Kontrol 5: Belge Tr TCKN ise uyruk TC olmal
            if (guest.uyruk.toUpperCase() !== 'TC') {
                errors.push({ id: uuidv4(), type: 'TC_UYRUK_MISMATCH', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin belgeTuru TCKN iken uyruk TC deil.`, guest });
            }

            // Kontrol 6: Belge Tr TCKN ise ikamet adresi Turkey olmal
            if (guest.ikametAdresi.toUpperCase() !== 'TURKEY') {
                errors.push({ id: uuidv4(), type: 'TC_IKAMET_MISMATCH', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin belgeTuru TCKN iken ikamet adresi Turkey deil.`, guest });
            }

            // Kontrol 7 & 10: BelgeNo 11 haneli ve say m?
            if (!isTc) {
                // Eer 9x ile balamyorsa ve 11 hane deilse hata ver
                if (!startsWith9x) {
                    errors.push({ id: uuidv4(), type: 'TC_BELGENO_INVALID', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin TCKN numaras 11 hane ve say olmal.`, guest });
                }
            } else if (startsWith9x && isTc) {
                // Kontrol 10: TCKN 97,98,99 ile balyorsa ve 11 haneli sayysa, BelgeTuru PAS olmalyd
                 errors.push({ id: uuidv4(), type: 'TCKN_9X_WARNING', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin belgeNo 9x ile balyor, bu TCKN yerine Pasaport olabilir.`, guest });
            }
        }
        
        // Kontrol 8, 9: Belge tr "PAS" olanlar iin
        if (guest.belgeTuru === 'PAS') {
            // Kontrol 8: Pasaport ise uyruk TC olamaz
            if (guest.uyruk.toUpperCase() === 'TC') {
                errors.push({ id: uuidv4(), type: 'PAS_UYRUK_TC', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin belgeTuru PAS iken uyruk TC.`, guest });
            }
            // Kontrol 9: Pasaport ise ikamet adresi Turkey olamaz
            if (guest.ikametAdresi.toUpperCase() === 'TURKEY') {
                errors.push({ id: uuidv4(), type: 'PAS_IKAMET_TURKEY', message: `Oda No: ${guest.roomNo}, Misafir: ${guestName} iin belgeTuru PAS iken ikamet adresi Turkey.`, guest });
            }
        }
    });

    return errors;
};
